            clickMeLink(){
                this.$router.push({path: '/history'});
            },
            classifyFn(type){
                this.active = type;
                if (type == 0) {
                    return this.newArr = this.ratings.filter((rating) => {
                        return rating;
                    });
                }else if(type == 1){
                    return this.newArr = this.ratings.filter((rating) => {
                        return rating.rateType === 0;
                    });
                }else if(type == 2){
                    return this.newArr = this.ratings.filter((rating) => {
                        return rating.rateType === 1;
                    });
